@include ipad-portrait {
    
    .edgt-related-posts-holder {
        
        .edgt-related-post {
            width: 50% !important;
        }
    }

    .edgt-blog-single-navigation .edgt-blog-single-next .edgt-blog-single-nav-title,
    .edgt-blog-single-navigation .edgt-blog-single-prev .edgt-blog-single-nav-title {
        display: none;
    }
}

@include phone-portrait {
    
    .edgt-related-posts-holder {
        
        .edgt-related-post {
            width: 100% !important;
        }
    }

    .edgt-blog-single-navigation{
        .edgt-blog-single-navigation-inner{
            margin: 20px 0 0;
            .edgt-blog-single-prev,
            .edgt-blog-single-next{
                .edgt-nav-blog-post-image{
                    display: none;
                }
            }
            .edgt-blog-single-next{
                .edgt-nav-blog-post-label-wrapper {
                    position: relative;
                    top: auto;
                    right: 0;
                    @include edgtTransform(translateY(0));
                }
            }
        }
    }
}