/* ==========================================================================
   Blog Slider shortcode responsive style - begin
   ========================================================================== */

@include ipad-landscape {
    
    .edgt-blog-slider-holder {
        
        .active {
            
            .edgt-blog-slider-item {
    
                .edgt-item-image:after,
                .edgt-item-text-wrapper {
                    opacity: 1;
                }
            }
        }
    }
}
/* ==========================================================================
   Blog Slider shortcode responsive style - end
   ========================================================================== */
